export default {
    state: {
        films: [],
        film: {},
    },
    getters: {
        films(state) {
            return state.films
        },
        film(state) {
            return state.film
        }
    },
    mutations: {
        setFilms(state, payload) {
            state.films = payload
        },
        setFilm(state, payload) {
            state.film = payload
        }
    },
    actions: {
        async getFilms({commit}) {
            let r = await fetch('https://videocdn.tv/api/movies?api_token=CMteVkivIq7oQPOpZtrpGIB7esvY2juc')
            let json = await r.json()
            console.log(json)
            commit('setFilms', json)
        },
        async getFilm({commit}, id) {
            let res = await fetch(`https://videocdn.tv/api/short?api_token=CMteVkivIq7oQPOpZtrpGIB7esvY2juc&kinopoisk_id=${id}`)
            console.log('res',res)
            let json = await res.json()
            console.log('film',json)
            commit('setFilm', json)
        },
    }
}