<template>
    <div class="preview">
        <div class="preview_wrapper" :style="{backgroundImage: `url(${moreFilm.Poster})`}">
            <div class="preview_q">{{film.media[0].max_quality}}</div>
        </div>
        <div class="preview_title">
            {{film.ru_title}} | {{film.orig_title}}
        </div>
    </div>
</template>
<script>
export default {
    props: {
        film: Object,
    },
    data() {
        return {
            img: '',
            moreFilm: {}
        }
    },
    async mounted() {
        let r = await fetch(`https://www.omdbapi.com/?apikey=d36d7f24&i=${this.film.imdb_id}`)
        this.moreFilm = await r.json()
    },
}
</script>
<style>
    .preview_wrapper {
        position: relative;
        width: 300px;
        height: 450px;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .preview_q {
        position: absolute;
        bottom: 5px;
        right: 5px;
        background-color: rgb(5, 86, 100);
        color: white;
        border-radius: 5px;
    }
</style>